* {
    margin: 0;
    padding: 0;
}

body {
    /*background-color: #333;
    color: #fff;*/
    font-family: 'TildaSans', Arial, sans-serif;
    font-size: 20px;
    /*padding: 25px 100px;*/
}

.header {
    position: fixed;
    top: 0;
    width: 100%;
    background-color: #43a5ee;
    padding: 5px 50px;
    font-weight: 300;
    transition: transform 0.3s ease-in-out;
    box-sizing: border-box;
}
.header.hidden {
    transform: translateY(-100%);
}

.header .header-menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header .header-icons {
    color: #43a5ee;
    display: flex;
    gap: 10px;
}

.header .header-icons .faVk,
.header .header-icons .faInsta,
.header .header-icons .faTelega,
.header .header-icons .faYou {
    border: 1px solid #fff;
    border-radius: 50%;
    background-color: #fff;
    padding: 5px;
    cursor: pointer;
}

.menu .list-menu {
    color: #fff;
    display: flex;
    gap: 20px;
    list-style-type: none;
    position: relative;
}
.list-menu__item {
    font-size: 16px;
}
.menu .list-menu li {
    cursor: pointer;
}

.menu-faculty, 
.menu-applicants, 
.menu-science,
.menu-teachers,
.menu-students {
    font-size: 10px;
    box-shadow: 0 0 5px 5px rgba(0, 0, 0, 0.5);
    padding: 10px;
    background: #fff;
    position: absolute;
    width: 120px;
    opacity: 0;
    transform: translateY(-10px);
    visibility: hidden;
    transition: 0.3s;
}

.menu-faculty.active,
.menu-applicants.active, 
.menu-science.active,
.menu-teachers.active,
.menu-students.active {
    margin-top: 10px;
    transform: translateY(0);
    opacity: 1;
    visibility: visible;
}

.menu-faculty .list-menu__faculty,
.menu-applicants .list-menu__applicants,
.menu-science .list-menu__science,
.menu-teachers .list-menu__teachers,
.menu-students .list-menu__students {
    color: #000;
    list-style-type: none;
    font-size: 14px;
}

.menu-faculty .list-menu__faculty li,
.menu-applicants .list-menu__applicants li,
.menu-science .list-menu__science li,
.menu-teachers .list-menu__teachers li, 
.menu-students .list-menu__students li {
    margin-bottom: 10px;
    cursor: pointer;
}

main {
    padding: 10px 100px;
    margin: 0 auto;
}
main h1 {
    color: #43a5ee;
    margin-bottom: 80px;
}
main .news {
    width: 300px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 40px;
    justify-content: space-between;
}

main .news-list {
    /*border-radius: 5px;*/
    box-shadow: 0 0 5px 5px rgba(0, 0, 0, 0.5);
}

main .news-list .image {
   margin-bottom: 20px;
}

main .news-list h6 {
    padding: 0 30px;
    line-height: 2;
}

main .news-list .about {
    font-size: 10px;
    padding: 0 30px;
    line-height: 2;
}

main .news-list .data {
    font-size: 8px;
    padding: 10px 30px;
}

.button-more {
    background: #43a5ee;
    color: #fff;
    border: none;
    border-radius: 10px;
    padding: 10px 40px;
    display: block;
    margin: 70px auto 20px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}
.button-more:disabled {
    background-color: #bfd5e6;
    cursor: not-allowed;
}

.footer {
    background-color: #43a5ee;
    padding: 70px 20px;
}

.info {
    display: flex;
    grid-template-columns: repeat(3, 1fr);
    justify-content: space-between;
    align-items: center;
}

.copyright, .footer-icons, .button-footer {
    width: 30%;
}
.copyright {
    color: #fff;
    font-size: 12px;
}
.footer-icons {
    display: flex;
    justify-content: center;
    gap: 10px;
    color: #c0e5ff;
    cursor: pointer;
}

.button-footer  {
    display: flex;
    justify-content: end;
    border: none;
    color: #fff;
    font-size: 12px;
    background-color: rgba(0, 0 ,0 ,0);
}

